.job{
  font-size: clamp(5vw, 48px, 39px) !important;
  font-weight: 800;
}
.home p {
  font-size: 24px;
}
#profile{
  max-height: 300px;
  border-radius: 32px;
}
@-webkit-keyframes fadein {
  30%,100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
@keyframes fadein {
  30%,100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
@media (max-width: 767px)   {
  .spline {
    width: 50% !important;
    padding-top: 63.33% !important; /* 4/3 of the width to maintain a 3:4 ratio */
  }
  .gap-5{
    gap: 0.5rem !important;
  }
  .row-loader{
    max-height: fit-content !important;
  }
  .spline-container{
    max-height: fit-content !important;
  }
}
